.event {
  margin-top: 50px;
  padding-top: 50px;
  border-top: 1px solid #dedddb; }

.event__date {
  font-size: 14px;
  font-family: "Whitney SSm A", "Whitney SSm B", verdana, sans-serif;
  font-weight: 600; }

.event__title {
  font-size: 20px;
  font-family: "Sentinel SSm A", "Sentinel SSm B", georgia, serif;
  font-weight: 500;
  margin-top: 0;
  margin-bottom: 0; }

.boxed-image {
  position: relative;
  padding-top: 80%;
  overflow: hidden; }
  @media only screen and (min-width: 30em) {
    .boxed-image {
      min-height: 480px;
      padding-top: inherit; } }
  @media only screen and (min-width: 48em) {
    .boxed-image {
      min-height: 550px; } }

.boxed-image__caption {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 2; }

.boxed-image__background-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center center;
  z-index: 1; }

.overlay-caption {
  font-size: 12px;
  padding: 10px;
  color: #fff;
  background-color: rgba(34, 34, 34, 0.4); }

.divider {
  height: 1px;
  background-color: #58544b;
  border: 0;
  margin-top: 50px;
  margin-bottom: 50px; }

.global-header {
  background-color: #00255c;
  height: 70px;
  position: relative;
  z-index: 3; }

.global-header-logo {
  text-align: center;
  margin: 0 auto;
  width: 65px;
  position: absolute;
  left: 50%;
  top: 10px;
  transform: translateX(-50%); }
  @media only screen and (min-width: 48em) {
    .global-header-logo {
      position: absolute;
      left: 15px;
      top: 13px;
      z-index: 3;
      transform: none; } }

.global-header-wordmark {
  display: none;
  width: 127px; }
  @media only screen and (min-width: 48em) {
    .global-header-wordmark {
      display: block;
      position: absolute;
      top: 26px;
      left: 92px; } }

.global-footer {
  background-color: #edebe1;
  padding-top: 50px;
  padding-bottom: 10px;
  text-align: center; }
  .global-footer strong {
    font-family: "Whitney SSm A", "Whitney SSm B", verdana, sans-serif;
    font-weight: 600; }
  @media only screen and (min-width: 48em) {
    .global-footer {
      text-align: left; } }
  .global-footer a {
    color: #000; }
  .global-footer ul {
    padding-left: 0;
    list-style-type: none; }
    .global-footer ul li {
      margin-bottom: 10px; }
      .global-footer ul li a {
        font-family: "Whitney SSm A", "Whitney SSm B", verdana, sans-serif;
        font-weight: 600; }

.footer-sponsor-box {
  margin-bottom: 50px; }

.footer-support-box {
  margin-bottom: 50px; }
  .footer-support-box p {
    margin-bottom: 0; }
  @media only screen and (min-width: 48em) {
    .footer-support-box {
      border-left: 1px solid lightgray;
      padding-left: 50px; } }

.footer-copyright {
  text-align: center; }

